<template>
    <div>
        <loading :active.sync="isLoading"></loading>
       <div class="text-right mt-4">
        <button class="btn btn-primary" @click="openModal(true)"></button> 
       </div>
       <table class="table mt-4">
         <thead>
          <tr>
            <th width="120"></th>
            <th></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="100"></th>
            <th width="80"></th>
          </tr>
         </thead>
            <tbody>
                <tr v-for="(item, key) in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency }}</td>
                    <td class="text-right">{{item.price | currency }}</td>
                    <td>
                     <span v-if="item.is_enabled" class="text-success"></span>
                     <span v-else></span>
                    </td>
                    <td>
                     <button class="btn btn-outline-success btn-sm" @click="openModal(false,item)"></button>
                    </td>
                </tr>
            </tbody>

       </table>
       <!---->
       <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item" :class="{'disabled':!pagination.has_pre}">
                <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page - 1)">
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                </a>
                </li>
                <li class="page-item" v-for="page in pagination.total_pages" :key="page" 
                :class="{'active':pagination.current_page===page}"
                @click.prevent="getProducts(page)">
                <a class="page-link" href="#">{{page}}</a>
                </li>
                <li class="page-item" :class="{'disabled':!pagination.has_next}">
                <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page + 1)">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
                </li>
            </ul>
        </nav>
       <!-- Modal -->
       <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div class="row">
                <div class="col-sm-12">
                    <div class="form-group">
                    <label for="image"></label>
                    <input type="text" class="form-control" id="image"
                        placeholder="" v-model="tempProduct.imageUrl">
                    </div>
                    <div class="form-group">
                    <label for="customFile"> 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control"
                        ref="files" @change="uploadFile">
                    </div>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="" :src="tempProduct.imageUrl">
                </div>
                <div class="col-sm-4">
                    <div class="form-group">
                    <label for="image"> 02 / </label>
                    <input type="text" class="form-control" id="image2"
                        placeholder="" v-model="tempProduct.fn2Url">
                    </div>
                    <div class="form-group">
                    <label for="customFile2"> 02 / 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile2" class="form-control"
                        ref="files2" @change="Imgfn2">
                    </div>
                    <label for="imgText2">02/ </label>
                    <textarea type="text" class="form-control" id="imgText2"
                        placeholder="02/ " v-model="tempProduct.imgText2"></textarea>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="" :src="tempProduct.fn2Url">
                </div>
                <div class="col-sm-4">
                    <div class="form-group">
                    <label for="image"> 03 / </label>
                    <input type="text" class="form-control" id="image3"
                        placeholder="" v-model="tempProduct.fn2Url">
                    </div>
                    <div class="form-group">
                    <label for="customFile3"> 03 / 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile3" class="form-control"
                        ref="files3" @change="Imgfn3">
                    </div>
                    <label for="imgText3">03/ </label>
                    <textarea type="text" class="form-control" id="imgText3"
                        placeholder="03/ " v-model="tempProduct.imgText3">
                    </textarea>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="" :src="tempProduct.fn3Url">
                </div>
                <div class="col-sm-4">
                    <div class="form-group">
                    <label for="image"> 04 / </label>
                    <input type="text" class="form-control" id="image4"
                        placeholder="" v-model="tempProduct.fn4Url">
                    </div>
                    <div class="form-group">
                    <label for="customFile4"> 04 / 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile4" class="form-control"
                        ref="files4" @change="Imgfn4">
                    </div>   
                    <div class="form-group">
                    <label for="imgText4">04/ </label>
                    <textarea type="text" class="form-control" id="imgText4"
                        placeholder="04/ " v-model="tempProduct.imgText4"></textarea>
                    </div>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="" :src="tempProduct.fn4Url">
                </div>
                
                
                <div class="col-sm-8">
                    <div class="form-group">
                    <label for="title"></label>
                    <input type="text" class="form-control" id="title"
                        placeholder="" v-model="tempProduct.title">
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                        <label for="category"></label>
                        <select class="form-control" id="category"
                        placeholder="" v-model="tempProduct.category">
                            <option></option>
                            <option></option>
                            <option></option>
                            <option></option>
                            <option></option>
                            <option></option>
                            <option></option>
                        </select>
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="unit" class="form-control" id="unit"
                        placeholder="" v-model="tempProduct.unit" >
                    </div>
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                        <input type="number" class="form-control" id="origin_price"
                        placeholder="" v-model="tempProduct.origin_price">
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="number" class="form-control" id="price"
                        placeholder="" v-model="tempProduct.price">
                    </div>
                    </div>
                    <hr>

                    <div class="form-group">
                    <label for="description"></label>
                    <textarea type="text" class="form-control" id="description"
                        placeholder="" v-model="tempProduct.description"></textarea>
                    </div>
                    <div class="form-group">
                    <label for="content"></label>
                    <textarea type="text" class="form-control" id="content"
                        placeholder="" v-model="tempProduct.content"></textarea>
                    </div>
                    <div class="form-group">
                    <label for="reminder">/</label>
                    <textarea type="text" class="form-control" id="detail"
                        placeholder=" /" v-model="tempProduct.detail"></textarea>
                    </div>
                    <div class="form-group">
                    <label for="reminder">/</label>
                    <textarea type="text" class="form-control" id="review"
                        placeholder=" /" v-model="tempProduct.review"></textarea>
                    </div>
                    <div class="form-group">
                    <label for="reminder"></label>
                    <textarea type="text" class="form-control" id="reminder"
                        placeholder="" v-model="tempProduct.reminder"></textarea>
                    </div>
                    <div class="form-group">
                    <div class="form-check">
                        <input class="form-check-input" type="checkbox"
                        id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                        <label class="form-check-label" for="is_enabled">
                        
                        </label>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-primary" @click="updateProduct"></button>
            </div>
            </div>
        </div>
        </div>
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger"></strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-danger"
                ></button>
            </div>
            </div>
        </div>
      </div>
      
    </div>  
</template>

<script>
import $ from 'jquery'; //main.jsbootstrap jquery  

export default {
    data(){
        return{
            products: [
            ], //products
            tempProduct: {
                reminder: '',
                detail: '',
                review: '',
                imgText2: '',
                imgText3: '',
                imgText4: ''
            }, // api
            isNew: false,
            isLoading: false, //
            status: { //loading
                fileUploading: false, //icon
            },
            pagination: {}, //console
        }
    },
    methods: {
        getProducts(page=1){ // //Productsproducts //page >> getProducts(page=1) 
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`; //${page} 
            const vm = this; //vm
            console.log(process.env.APIPATH,process.env.CUSTOMPATH);
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
            console.log(response.data);
            vm.isLoading = false;
            vm.products = response.data.products; //Vueget
            vm.pagination = response.data.pagination;
            })
        },
        openModal(isNew, item){ //
            if (isNew){
                this.tempProduct={
                    reminder,
                    detail,
                    review,
                    imgText2,
                    imgText3,
                    imgText4
                };
                this.isNew=true
            }else{
                this.tempProduct=Object.assign({},item); //ES6 item{} this.temProductitem
                this.isNew=false
            }
            $('#productModal').modal('show');  //methodsModal(Modal)
        },
        uploadFile(){
            console.log(this);
            const uploadedFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();
            formData.append('file-to-upload',uploadedFile); //appendfile-to-upload uploadFile
            const  api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            vm.status.fileUploading = true;
            this.$http.post(api, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }).then((response)=>{
                console.log(response.data);
                vm.status.fileUploading = false;
                if (response.data.success){
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl); //vm.tempProductimageUrlresponse.data.imageUrl
                }else{
                     this.$bus.$emit('message:push',response.data.message,'danger');

                }
            })
        },
        Imgfn2() {
        console.log(this);
        const Img2 = this.$refs.files2.files[0];
        const vm = this;
        const formData = new FormData();
        formData.append('file-to-upload',Img2); //appendfile-to-upload uploadFile
        const  api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
        vm.status.fileUploading = true;
        this.$http.post(api, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then((response)=>{
            console.log(response.data);
            vm.status.fileUploading = false;
            if (response.data.success){
                vm.$set(vm.tempProduct, 'fn2Url', response.data.imageUrl); //vm.tempProductimageUrlresponse.data.imageUrl
            }else{
                    this.$bus.$emit('message:push',response.data.message,'danger');

            }
        })
        },
        Imgfn3() {
        console.log(this);
        const Img3 = this.$refs.files3.files[0];
        const vm = this;
        const formData = new FormData();
        formData.append('file-to-upload',Img3); //appendfile-to-upload uploadFile
        const  api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
        vm.status.fileUploading = true;
        this.$http.post(api, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then((response)=>{
            console.log(response.data);
            vm.status.fileUploading = false;
            if (response.data.success){
                vm.$set(vm.tempProduct, 'fn3Url', response.data.imageUrl); //vm.tempProductimageUrlresponse.data.imageUrl
            }else{
                    this.$bus.$emit('message:push',response.data.message,'danger');

            }
        })
        },
        Imgfn4() {
        console.log(this);
        const Img4 = this.$refs.files4.files[0];
        const vm = this;
        const formData = new FormData();
        formData.append('file-to-upload',Img4); //appendfile-to-upload uploadFile
        const  api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
        vm.status.fileUploading = true;
        this.$http.post(api, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then((response)=>{
            console.log(response.data);
            vm.status.fileUploading = false;
            if (response.data.success){
                vm.$set(vm.tempProduct, 'fn4Url', response.data.imageUrl); 
            }else{
                    this.$bus.$emit('message:push',response.data.message,'danger');

            }
        })
        },
        updateProduct(){
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`; //api
            let httpMethod= 'post';
            const vm = this; //vm
            if (!vm.isNew){ //api
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                httpMethod= 'put'
            }
            console.log(process.env.APIPATH,process.env.CUSTOMPATH);
            this.$http[httpMethod](api, {data: vm.tempProduct }).then((response) => { //post get 
            console.log(response.data);
            // vm.products = response.data.products; //Vueget
            if (response.data.success){ //
                $('#productModal').modal('hide'); //modal
                vm.getProducts();  //
            }else{
                $('#productModal').modal('hide');
                vm.getProducts();
                console.log(''); //console
            }
            })
        }
    },
    created(){
        this.getProducts();
    }
}
</script>



<style>
button.btn.btn-primary {
    background-color: green;
    border-radius: 20px 20px 20px 20px;
    padding: 5px 20px;
}
button.btn.btn-outline-secondary {
    border-radius: 20px 20px 20px 20px;
    padding: 5px 20px;
}
button.btn.btn-outline-success.btn-sm{
    border-radius: 20px 20px 20px 20px;
    padding: 5px 15px;
    
}
</style>



